.mainContainer {
  width: 100vw;
  height: 100vh;
}

.tlContainer {
  color: #1b1c1e; 
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.tlWindow {
  background: #f3dfa6;
  width: 30%;
  height: 100%;
  padding: 2rem 0;
  margin: 0 auto;

  .title {
    margin-bottom: 1rem;
    padding: 0 1rem;
  }
}

.tlWindowList {
  list-style: none;

  li {
    cursor: pointer;
    padding: 0.5rem 1rem;
    transition: 300ms ease;
  }

  span.badge {
    background: #98583c;
    border-radius: 8px;
    color: #fff;
    font-size: 0.8125rem;
    font-weight: 500;
    margin-left: 1rem;
    padding: 0.25rem 0.5rem;
  }

  li.selected {
    background: #98583c;
    color: #fff;

    span.badge {
      background: #fff;
      color: #98583c;
    }
  }
}

.tlTasksContainer {
  background: url('./../../assets/pawprintbg.jpg') no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70%;
  height: 100%;
}

.tlTasks {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 8px #1b1c1e50;
  margin: 0 auto;
  padding: 1.5rem 1rem;
  width: 80%;
  height: 90%;
  position: relative;
  overflow: hidden;
}

.tlList,
.tlCompleted {
  overflow-y: scroll;
}

.title {
  font-size: 1.5rem;
  font-weight: 500;
  margin-bottom: 2rem;

  span {
    color: #4c5760;
    font-size: 0.8rem;
    font-weight: 400;
    margin-left: 1rem;
  }
}

.emptyText {
  text-align: center;
}

.border {
  position: absolute;
  bottom: -30px;
  left: 0;
  opacity: 1;
  width: 100%;

  img {
    width: 100%;
  }
}

@media screen and (max-width: 767px) {
  .tlContainer {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .tlWindow {
    height: 15%;
    width: 100%;
    padding: 1rem 0;
  }

  .tlWindowList {
    background: #fff2cc;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    li {
      width: 50%;
      font-size: 0.825rem;
    }
  }

  .tlTasksContainer {
    padding: 2rem 0;
    width: 100%;
    height: 100%;
  }

  .title {
    margin-bottom: 1rem;
  }

  .border {
    left: 25%;
    opacity: 0;
    transition: 300ms ease;

    img {
      width: 50%;
    }
  }
}